import { NextResponse } from 'next/server';
import pool from '../../../lib/db'; // ‚úÖ ‡πÉ‡∏ä‡πâ @/lib/db ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏Ñ‡∏ß‡∏≤‡∏°‡∏ä‡∏±‡∏ß‡∏£‡πå‡πÄ‡∏£‡∏∑‡πà‡∏≠‡∏á Path

// 1. GET: ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏Ñ‡∏π‡πà‡∏Ñ‡πâ‡∏≤
export async function GET(request) {
  try {
    const { searchParams } = new URL(request.url);
    const search = searchParams.get('search') || '';

    let sql = `SELECT * FROM suppliers`;
    let params = [];

    if (search) {
      // ‡∏Ñ‡πâ‡∏ô‡∏´‡∏≤‡∏Ñ‡∏£‡∏≠‡∏ö‡∏Ñ‡∏•‡∏∏‡∏°‡∏ñ‡∏∂‡∏á‡∏ä‡∏∑‡πà‡∏≠‡∏ú‡∏π‡πâ‡∏ï‡∏¥‡∏î‡∏ï‡πà‡∏≠‡πÅ‡∏•‡∏∞‡πÄ‡∏ö‡∏≠‡∏£‡πå‡πÇ‡∏ó‡∏£
      sql += ` WHERE name LIKE ? OR code LIKE ? OR contact_name LIKE ? OR phone LIKE ?`;
      params = [`%${search}%`, `%${search}%`, `%${search}%`, `%${search}%`];
    }
    
    sql += ` ORDER BY id DESC`;

    const [rows] = await pool.query(sql, params);
    return NextResponse.json(rows);
  } catch (error) {
    return NextResponse.json({ error: error.message }, { status: 500 });
  }
}

// 2. POST: ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏Ñ‡∏π‡πà‡∏Ñ‡πâ‡∏≤‡πÉ‡∏´‡∏°‡πà (‚úÖ ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏Å‡∏≤‡∏£‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏ó‡∏µ‡πà‡∏≠‡∏¢‡∏π‡πà‡πÅ‡∏¢‡∏Å‡∏™‡πà‡∏ß‡∏ô)
export async function POST(request) {
    try {
        const body = await request.json();
        
        // ‡∏£‡∏±‡∏ö‡∏Ñ‡πà‡∏≤ 4 ‡∏ï‡∏±‡∏ß‡πÉ‡∏´‡∏°‡πà (sub_district, district, province, zipcode)
        const { 
            code, name, contact_name, 
            address, sub_district, district, province, zipcode, // <--- ‡∏°‡∏≤‡πÉ‡∏´‡∏°‡πà
            phone, email, tax_id, credit_term 
        } = body;

        // Validation
        if (!code || !name) {
            return NextResponse.json({ error: '‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏£‡∏´‡∏±‡∏™‡πÅ‡∏•‡∏∞‡∏ä‡∏∑‡πà‡∏≠‡∏Ñ‡∏π‡πà‡∏Ñ‡πâ‡∏≤' }, { status: 400 });
        }

        // ‡πÄ‡∏ä‡πá‡∏Ñ Code ‡∏ã‡πâ‡∏≥
        const [existing] = await pool.query('SELECT id FROM suppliers WHERE code = ?', [code]);
        if (existing.length > 0) {
            return NextResponse.json({ error: '‡∏£‡∏´‡∏±‡∏™‡∏Ñ‡∏π‡πà‡∏Ñ‡πâ‡∏≤‡∏ô‡∏µ‡πâ‡∏°‡∏µ‡∏≠‡∏¢‡∏π‡πà‡πÅ‡∏•‡πâ‡∏ß' }, { status: 400 });
        }

        const sql = `
            INSERT INTO suppliers 
            (
                code, name, contact_name, 
                address, sub_district, district, province, zipcode, 
                phone, email, tax_id, credit_term
            ) 
            VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)
        `;
        
        const values = [
            code, 
            name, 
            contact_name || '', 
            address || '',          // ‡∏ó‡∏µ‡πà‡∏≠‡∏¢‡∏π‡πà‡∏ö‡∏£‡∏£‡∏ó‡∏±‡∏î‡πÅ‡∏£‡∏Å (‡πÄ‡∏•‡∏Ç‡∏ó‡∏µ‡πà/‡∏´‡∏°‡∏π‡πà‡∏ö‡πâ‡∏≤‡∏ô)
            sub_district || '',     // ‡∏ï‡∏≥‡∏ö‡∏•
            district || '',         // ‡∏≠‡∏≥‡πÄ‡∏†‡∏≠
            province || '',         // ‡∏à‡∏±‡∏á‡∏´‡∏ß‡∏±‡∏î
            zipcode || '',          // ‡∏£‡∏´‡∏±‡∏™‡πÑ‡∏õ‡∏£‡∏©‡∏ì‡∏µ‡∏¢‡πå
            phone || '', 
            email || '', 
            tax_id || '', 
            credit_term || 0
        ];

        await pool.query(sql, values);
        return NextResponse.json({ success: true, message: '‡∏ö‡∏±‡∏ô‡∏ó‡∏∂‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏™‡∏≥‡πÄ‡∏£‡πá‡∏à' });

    } catch (error) {
        console.error("Database Error:", error);
        return NextResponse.json({ error: error.message }, { status: 500 });
    }
}

// 3. PUT: ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏• (‚úÖ ‡πÄ‡∏û‡∏¥‡πà‡∏°‡∏Å‡∏≤‡∏£‡∏≠‡∏±‡∏õ‡πÄ‡∏î‡∏ï‡∏ó‡∏µ‡πà‡∏≠‡∏¢‡∏π‡πà‡πÅ‡∏¢‡∏Å‡∏™‡πà‡∏ß‡∏ô)
export async function PUT(request) {
    try {
        const body = await request.json();
        const { 
            id, code, name, contact_name, 
            address, sub_district, district, province, zipcode, // <--- ‡∏°‡∏≤‡πÉ‡∏´‡∏°‡πà
            phone, email, tax_id, credit_term 
        } = body;

        const sql = `
            UPDATE suppliers SET 
            code=?, name=?, contact_name=?, 
            address=?, sub_district=?, district=?, province=?, zipcode=?, 
            phone=?, email=?, tax_id=?, credit_term=? 
            WHERE id=?
        `;
        
        const values = [
            code, name, contact_name, 
            address, sub_district, district, province, zipcode,
            phone, email, tax_id, credit_term, 
            id
        ];

        await pool.query(sql, values);
        return NextResponse.json({ success: true });

    } catch (error) {
        return NextResponse.json({ error: error.message }, { status: 500 });
    }
}

// 4. DELETE: ‡∏•‡∏ö‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•
export async function DELETE(request) {
    try {
        const { searchParams } = new URL(request.url);
        const id = searchParams.get('id');
        
        if (!id) return NextResponse.json({ error: 'Missing ID' }, { status: 400 });

        await pool.query('DELETE FROM suppliers WHERE id = ?', [id]);
        return NextResponse.json({ success: true });
    } catch (error) {
        return NextResponse.json({ error: error.message }, { status: 500 });
    }
}